'use client';

import { db, products } from '@/lib/db';
import { eq } from 'drizzle-orm';
import Image from 'next/image';
import Link from 'next/link';
import { notFound } from 'next/navigation';
import { useState } from 'react';

interface ProductPageProps {
  params: {
    id: string;
  };
}

async function getProduct(id: string) {
  const product = await db.select().from(products).where(eq(products.id, parseInt(id))).limit(1);
  return product[0];
}

async function getRelatedProducts(relatedIds: number[] | null) {
  if (!relatedIds?.length) return [];
  return db.select().from(products).where(eq(products.id, relatedIds[0]));
}

export default async function ProductPage({ params }: ProductPageProps) {
  const product = await getProduct(params.id);

  if (!product) {
    notFound();
  }

  const relatedProducts = await getRelatedProducts(product.relatedProducts);
  const allImages = [product.image];

  return (
    <div className="min-h-screen bg-gray-50 py-12">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="bg-white rounded-lg shadow-lg overflow-hidden">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-8 p-8">
            {/* Product Images */}
            <div className="space-y-4">
              {/* Main Image */}
              <div className="relative aspect-square group">
                <div className="absolute inset-0 overflow-hidden rounded-lg">
                  <Image
                    src={product.image}
                    alt={product.name}
                    fill
                    className="object-cover transition-transform duration-300 group-hover:scale-110"
                    sizes="(max-width: 768px) 100vw, 50vw"
                    priority
                  />
                </div>
                <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-10 transition-opacity duration-300 rounded-lg" />
              </div>

              {/* Thumbnails */}
              {allImages.length > 1 && (
                <div className="grid grid-cols-4 gap-4">
                  {allImages.map((image, index) => (
                    <button
                      key={index}
                      className="relative aspect-square rounded-lg overflow-hidden border-2 border-transparent hover:border-indigo-500 focus:outline-none focus:border-indigo-500"
                    >
                      <Image
                        src={image}
                        alt={`${product.name} - Image ${index + 1}`}
                        fill
                        className="object-cover"
                        sizes="(max-width: 768px) 25vw, 12vw"
                      />
                    </button>
                  ))}
                </div>
              )}
            </div>

            {/* Product Details */}
            <div className="flex flex-col justify-between">
              <div>
                <h1 className="text-3xl font-bold text-gray-900 mb-4">{product.name}</h1>
                <p className="text-gray-600 mb-6">{product.description}</p>
                
                <div className="space-y-4">
                  <div className="flex items-center">
                    <span className="text-gray-600">Price:</span>
                    <span className="ml-2 text-2xl font-bold text-indigo-600">
                      ${Number(product.price).toFixed(2)}
                    </span>
                  </div>
                  
                  <div className="flex items-center">
                    <span className="text-gray-600">Rating:</span>
                    <div className="ml-2 flex items-center">
                      <span className="text-yellow-400">★</span>
                      <span className="ml-1 text-gray-600">{Number(product.rating).toFixed(1)}</span>
                      <span className="ml-2 text-gray-500">({product.reviews} reviews)</span>
                    </div>
                  </div>
                  
                  <div className="flex items-center">
                    <span className="text-gray-600">Category:</span>
                    <span className="ml-2 text-gray-900">{product.category}</span>
                  </div>
                  
                  <div className="flex items-center">
                    <span className="text-gray-600">Age Range:</span>
                    <span className="ml-2 text-gray-900">{product.ageRange}</span>
                  </div>
                  
                  <div className="flex items-center">
                    <span className="text-gray-600">Pieces:</span>
                    <span className="ml-2 text-gray-900">{product.pieces}</span>
                  </div>
                  
                  <div className="flex items-center">
                    <span className="text-gray-600">Stock:</span>
                    <span className={`ml-2 ${product.inStock ? 'text-green-600' : 'text-red-600'}`}>
                      {product.inStock ? 'In Stock' : 'Out of Stock'}
                    </span>
                  </div>
                </div>
              </div>

              <div className="mt-8">
                <button
                  className={`w-full py-3 px-8 rounded-md font-medium text-white 
                    ${product.inStock 
                      ? 'bg-indigo-600 hover:bg-indigo-700' 
                      : 'bg-gray-400 cursor-not-allowed'
                    }`}
                  disabled={!product.inStock}
                >
                  {product.inStock ? 'Add to Cart' : 'Out of Stock'}
                </button>
              </div>
            </div>
          </div>
        </div>

        {/* Related Products */}
        {relatedProducts.length > 0 && (
          <div className="mt-12">
            <h2 className="text-2xl font-bold text-gray-900 mb-6">Related Products</h2>
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              {relatedProducts.map((relatedProduct) => (
                <Link 
                  key={relatedProduct.id} 
                  href={`/product/${relatedProduct.id}`}
                  className="bg-white rounded-lg shadow-md overflow-hidden hover:shadow-lg transition-shadow"
                >
                  <div className="relative aspect-[4/3] group">
                    <Image
                      src={relatedProduct.image}
                      alt={relatedProduct.name}
                      fill
                      className="object-cover transition-transform duration-300 group-hover:scale-110"
                      sizes="(max-width: 768px) 100vw, 33vw"
                    />
                    <div className="absolute inset-0 bg-black bg-opacity-0 group-hover:bg-opacity-10 transition-opacity duration-300" />
                  </div>
                  <div className="p-4">
                    <h3 className="text-lg font-semibold text-gray-900 mb-2">
                      {relatedProduct.name}
                    </h3>
                    <div className="flex justify-between items-center">
                      <span className="text-indigo-600 font-bold">
                        ${Number(relatedProduct.price).toFixed(2)}
                      </span>
                      <div className="flex items-center">
                        <span className="text-yellow-400">★</span>
                        <span className="ml-1 text-gray-600">{Number(relatedProduct.rating).toFixed(1)}</span>
                      </div>
                    </div>
                  </div>
                </Link>
              ))}
            </div>
          </div>
        )}
      </div>
    </div>
  );
} 